.questions {
  &--m {
    background-color: rgba($black, 0.6);
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 100;
    overflow: scroll;
  }
}

.questions__box {
  font-family: $font;
  padding-top: 87px;
  padding-bottom: 75px;

  &--m {
    position: absolute;
    background-color: $white;
    width: 100%;
    min-height: 803px;
    top: 0;
    animation: modal 700ms ease-in forwards;
  }

  @media (max-width: $s-end) {
    padding-top: 20px;
  }
}

.questions__layout {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-bottom: 75px;

  @media (max-width: $m-end) {
    margin-bottom: 16px;
  }
}

.questions__item {
  flex: 0 1 auto;
  margin-right: 19px;

  &:last-child {
    margin-right: 0;
  }

  @media (max-width: $m-end) {
    margin-right: 0;
  }
}


.questions__return-layout {
  display: flex;
  align-items: center;
  justify-content: flex-start;

  &:hover,
  &:focus {
    outline: none;

    & .questions__return-svg {
      transform: scale(1.06) translateX(-10px);
      transition: all 300ms ease-in;
    }
  }

  @media (max-width: $m-end) {
    justify-content: center;
  }
}

.questions__return-svg {
  fill: none;
  margin-right: 14px;

  @media (max-width: $s-end) {
    width: 10px;
    height: 20px;
  }
}

.questions__return-text {
  font-style: normal;
  font-weight: 600;
  font-size: 28px;
  line-height: 130%;
  color: $black;

  &:focus {
    outline: none;
  }

  @media (max-width: $s-end) {
    font-size: 14px;
  }
}
